}

.home {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home__text {
    font-size: 18px;
    border-radius: 5px;
    padding: 8px;
    background-color: #F0F0F0;
}

.collapsed {
    cursor: pointer;
}

.show-popup {
    display: block;
}

.image img {
    max-width: 300px;
    max-height: 300px;
}

.loader-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.spin-loader {
    border: 5px solid #f3f3f3; /* Light grey */
    border-top: 5px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.infinite-scroll-component {
    overflow: hidden !important;
}

.scroll-top-bottom {
    position: absolute;
    z-index: 5;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: none;
    outline: none;
    background: #2C2C2C;
    color: #fff;
    cursor: pointer;
    bottom: 145px;
    right: 2%;
    box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
}

.scroll-top-bottom .fa-chevron-down {
    color: #fff;
}

.count-new-messages {
    position: absolute;
    color: #fff;
    top: -8px;
    left: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
    border-radius: 10px;
    z-index: 11;
    height: 25px;
    background: #2C2C2C;
    box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
